let loading = 0;
let load_complete: () => void;
let loaded = new Promise<void>(resolve => load_complete = resolve);

function load_image(url: string) {
  loading += 1;

  const image = new Image();
  image.src = url;
  image.onload = () => {
    if ((loading -= 1) == 0) {
      load_complete();
    }
  };

  return image;
}

const melon_image = load_image(melon_asset);
const grapes_image = load_image(grapes_asset);
const pineapple_image = load_image(pineapple_asset);
const banana_image = load_image(banana_asset);
const strawberry_image = load_image(strawberry_asset);
const eggplant_image = load_image(eggplant_asset);
const fakegrapes_image = load_image(fakegrapes_asset);

let context: CanvasRenderingContext2D;
let score_ctx: CanvasRenderingContext2D;
let previous = 0;
let background_music = new Audio(background_asset);
let in_menu = true;

let player: Player;
let score: number;

let tetronimos: Tetronimo[] = [];
let queue: any[] = [];

function play_audio(audio: HTMLAudioElement) {
  audio.currentTime = 0;
  audio.play();
}

interface Tile {
  offset: Vec;
  image_offset: Vec;
}

interface Tetronimo {
  pos: Vec;
  sprite: HTMLImageElement;
  state: number;
  size: number;
  tiles: Tile[];
  rotation_offset: number;
  grounded: boolean;
}

interface Player {
  pos: Vec;
  size: Vec;
  vel: Vec;
  jumping: boolean;
  jump_buffer: number;
  facing: number;
  grounded: boolean;
  ceilinged: boolean;
  target: Tetronimo | null;
  push_cooldown: number;
  rotate_cooldown: number;
}

