function new_falling() {
  if (queue.length == 0) {
    let pool = [Tetronimo.I, Tetronimo.J, Tetronimo.L, Tetronimo.O, Tetronimo.S, Tetronimo.T, Tetronimo.Z];
    while (pool.length > 0) {
      let proto = pool.splice(Math.floor(Math.random() * pool.length), 1)[0];
      queue.push(proto);
    }
  }

  let proto: typeof Tetronimo.I = queue.shift()!;

  let x = Math.floor(Math.random() * (10 - proto.size));
  let y = -2;

  tetronimos.push({
    pos: new Vec(x, y),
    size: proto.size,
    sprite: proto.sprite,
    tiles: proto.shape.map((v: Vec, i: number) => ({ offset: v, image_offset: proto.images[i] })),
    state: 0,
    rotation_offset: proto.rotation_offset,
    grounded: false,
  });
}

function draw_tetronimo(t: Tetronimo) {
  for (let tile of t.tiles) {
    let o = Vec.scale(Vec.add(t.pos, tile.offset), 32);

    let angle = -(t.state + t.rotation_offset) / 2 * Math.PI;
    context.translate(o.x + consts.UNIT / 2, o.y + consts.UNIT / 2);
    context.rotate(angle);

    // context.fillRect(-consts.UNIT / 2, -consts.UNIT / 2, consts.UNIT, consts.UNIT);
    context.drawImage(t.sprite,
      tile.image_offset.x * consts.UNIT, tile.image_offset.y * consts.UNIT,
      consts.UNIT, consts.UNIT,
      -consts.UNIT / 2, -consts.UNIT / 2,
      consts.UNIT, consts.UNIT
    );

    context.restore();
    context.resetTransform();
  }
}

const kick_states: { [size: number]: Vec[][] } = {
  3: [
    [new Vec(-1, 0), new Vec(-1, -1), new Vec(0, +2), new Vec(-1, +2)],
    [new Vec(+1, 0), new Vec(+1, +1), new Vec(0, -2), new Vec(+1, -2)],
    [new Vec(+1, 0), new Vec(+1, -1), new Vec(0, +2), new Vec(+1, +2)],
    [new Vec(-1, 0), new Vec(-1, +1), new Vec(0, -2), new Vec(-1, -2)],
  ],
  4: [
    [new Vec(-2, 0), new Vec(+1, 0), new Vec(-2, +1), new Vec(+1, -2)],
    [new Vec(-1, 0), new Vec(+2, 0), new Vec(-1, -2), new Vec(+2, +1)],
    [new Vec(+2, 0), new Vec(-1, 0), new Vec(+2, -1), new Vec(-1, +2)],
    [new Vec(+1, 0), new Vec(-2, 0), new Vec(+1, +2), new Vec(-2, -1)],
  ]
}

function rotate(t: Tetronimo, is_right: boolean) {
  let new_state = t.state;
  if (is_right) new_state -= 1;
  else new_state += 1;

  new_state = new_state % 4;
  if (new_state < 0) new_state = 3;

  let rotated = [];

  let kicks;
  if (is_right) {
    kicks = [new Vec(0, 0), ...(kick_states[t.size]?.[t.state] ?? [])];

    for (let tile of t.tiles) {
      rotated.push({
        offset: new Vec(t.size - 1 - tile.offset.y, tile.offset.x),
        image_offset: tile.image_offset,
      });
    }
  } else {
    kicks = [new Vec(0, 0), ...(kick_states[t.size]?.[new_state] ?? [])];
    kicks = kicks.map(v => Vec.scale(v, -1));

    for (let tile of t.tiles) {
      rotated.push({
        offset: new Vec(tile.offset.y, t.size - 1 - tile.offset.x),
        image_offset: tile.image_offset,
      });
    }
  }

  let oldTiles = t.tiles;
  t.tiles = rotated;

  let oldPos = t.pos;

  let found = false;

  for (let kick of kicks) {
    t.pos = Vec.add(oldPos, kick);
    if (!collide_object(t)) {
      t.state = new_state;
      found = true;
      play_audio(block_rotate_sound);
      break;
    }
  }

  if (!found) {
    console.log("Cannot rotate");
    t.tiles = oldTiles;
    t.pos = oldPos;
  }
}

function collide_player(p: Player, movement: Vec): Vec | null {
  if (p.pos.x < 0 && movement.x) {
    return new Vec(0 - p.pos.x, 0);
  }

  if (p.pos.x + p.size.x > consts.WIDTH && movement.x) {
    return new Vec(-(p.pos.x + p.size.x - consts.WIDTH), 0);
  }

  if (p.pos.y + p.size.y > consts.HEIGHT && movement.y) {
    return new Vec(0, -(p.pos.y + p.size.y - consts.HEIGHT));
  }

  for (let t of tetronimos) {
    for (let tile of t.tiles) {
      let o = Vec.add(t.pos, tile.offset);

      if (o.x < p.pos.x + p.size.x && o.x + 1 > p.pos.x &&
        o.y < p.pos.y + p.size.y && o.y + 1 > p.pos.y) {
        if (movement.x > 0)
          return new Vec(-(p.pos.x + p.size.x - o.x), 0);

        if (movement.x < 0)
          return new Vec(o.x + 1 - p.pos.x, 0);

        if (movement.y > 0)
          return new Vec(0, -(p.pos.y + p.size.y - o.y));

        if (movement.y < 0)
          return new Vec(0, o.y + 1 - p.pos.y);
      }
    }
  }

  return null;
}

function collide_object(o: Tetronimo): Vec | null {
  for (let tile of o.tiles) {
    let pos = Vec.add(o.pos, tile.offset);

    if (pos.x < 0) {
      return new Vec(0 - pos.x, 0);
    }

    if (pos.x + 1 > consts.WIDTH) {
      return new Vec(pos.x + 1 - consts.WIDTH, 0);
    }

    if (pos.y + 1 > consts.HEIGHT) {
      return new Vec(0, -(pos.y + 1 - consts.HEIGHT));
    }

    for (let t of tetronimos) {
      if (t == o) continue;

      for (let tile of t.tiles) {
        let o = Vec.add(t.pos, tile.offset);

        if (o.x < pos.x + 1 && o.x + 1 > pos.x &&
          o.y < pos.y + 1 && o.y + 1 > pos.y) {
          return new Vec(0, -(pos.y + 1 - o.y));
        }
      }
    }

    if (pos.x < player.pos.x + player.size.x && pos.x + 1 > player.pos.x &&
      pos.y < player.pos.y + player.size.y && pos.y + 1 > player.pos.y) {
      return new Vec(0, -(pos.y + 1 - player.pos.y));
    }
  }

  return null;
}

function collide_target(pos: Vec, size: Vec): Tetronimo | null {
  for (let t of tetronimos) {
    for (let tile of t.tiles) {
      let o = Vec.add(t.pos, tile.offset);

      if (o.x < pos.x + size.x && o.x + 1 > pos.x &&
        o.y < pos.y + size.y && o.y + 1 > pos.y) {

        return t;
      }
    }
  }

  return null;
}

function start_jump() {
  player.jump_buffer = 0;

  player.vel = new Vec(player.vel.x, -consts.JUMP_SPEED);
  play_audio(player_jump_sound);
}

input.onJump(() => {
  if (player.grounded) {
    start_jump();
  } else {
    player.jump_buffer = consts.BUFFER_TIME;
  }
});

input.onRotate(() => {
  do_rotate();
});

function do_rotate() {
  if (player.target == null) return;
  if (player.rotate_cooldown > 0) return;
  if (player.target.tiles.length != 4) return;

  rotate(player.target, player.facing < 1);
}

function find_full_lines() {
  let arr = [];

  for (let i = 0; i < consts.HEIGHT; i++) {

    let row_filled = true;
    for (let j = 0; j < consts.WIDTH; j++) {

      let found = false;
      for (let tet of tetronimos) {
        if (!tet.grounded) continue;

        if (!found) {
          for (let tile of tet.tiles) {
            let pos = Vec.round(Vec.add(tet.pos, tile.offset));
            if (pos.x == j && pos.y == i) {
              found = true;
              break;
            }
          }
        }
      }
      if (!found) {
        row_filled = false;
        break;
      }
    }
    if (row_filled) arr.push(i);
  }

  if (arr.length > 0) {
    for (let i of arr) {
      console.log("Line " + i.toString() + " is full");
    }
  }

  return arr;
}

function clear_lines(lines: number[]) {
  if (lines.length > 0) {
    if (Math.random() < 0.01)
      play_audio(line_clear_meme_sound)
    else if (lines.length == 1)
      play_audio(line_clear_sound);
    else if (lines.length == 2)
      play_audio(line_double_sound);
    else if (lines.length == 3)
      play_audio(line_triple_sound);
    else if (lines.length == 4)
      play_audio(line_quad_sound);
  }

  let multiplier = lines.length;

  score += multiplier * (lines.length * 10);

  for (let line of lines) {
    for (let j = tetronimos.length - 1; j >= 0; j--) {
      for (let k = tetronimos[j].tiles.length - 1; k >= 0; k--) {
        if (round(tetronimos[j].tiles[k].offset.y + tetronimos[j].pos.y) == line) {
          tetronimos[j].tiles.splice(k, 1);
        }
      }
      if (tetronimos[j].tiles.length == 0) {
        tetronimos.splice(j, 1);
      }
    }

    for (let t of tetronimos) {
      if (!t.grounded) continue;

      for (let tile of t.tiles) {
        let pos = Vec.add(t.pos, tile.offset);
        if (pos.y < line) {
          tile.offset = Vec.add(tile.offset, new Vec(0, 1));
        }
      }
    }
  }
}

function tick(time: number) {
  let cancel = requestAnimationFrame(tick); // keep going forever

  score_ctx.font = "bold 20px Orbitron";
  score_ctx.textAlign = "center";
  score_ctx.fillStyle = "black";
  score_ctx.clearRect(0, 0, consts.SCORE_ABSOLUTE_WIDTH, consts.SCORE_ABSOLUTE_HEIGHT);
  score_ctx.fillText("Score: " + score.toString(), consts.SCORE_ABSOLUTE_WIDTH / 2, consts.SCORE_ABSOLUTE_HEIGHT / 2);

  time /= 1000;
  let delta = previous ? time - previous : 0;
  previous = time;

  let landed = false;
  for (let t of tetronimos.slice().reverse()) {
    t.pos = Vec.add(t.pos, new Vec(0, delta * consts.FALL_SPEED));
    let wasGrounded = t.grounded;
    t.grounded = false;

    let collision = collide_object(t);
    if (collision) {
      t.pos = Vec.round(Vec.add(t.pos, collision));

      t.grounded = true;
      if (!wasGrounded) {
        play_audio(block_landing_sound);
      }

      if (tetronimos[tetronimos.length - 1] == t) {
        landed = true;
      }
    }
  }

  let lines = find_full_lines();

  clear_lines(lines);

  if (landed) {
    new_falling();
  }

  let wasCeilinged = player.ceilinged;
  let wasGrounded = player.grounded;
  player.grounded = false;
  player.ceilinged = false;

  { // move & slide
    player.pos = Vec.add(player.pos, new Vec(player.vel.x * delta, 0));
    let hit = collide_player(player, new Vec(player.vel.x, 0));
    if (hit) {
      player.pos = Vec.add(player.pos, hit);
      player.vel = new Vec(0, player.vel.y);
    }

    player.pos = Vec.add(player.pos, new Vec(0, player.vel.y * delta));
    hit = collide_player(player, new Vec(0, player.vel.y));
    if (hit) {
      player.pos = Vec.add(player.pos, hit);
      player.vel = new Vec(player.vel.x, 0);
      player.grounded = hit.y < 0;
      player.ceilinged = hit.y > 1;

      if (player.ceilinged && !wasCeilinged) {
        play_audio(player_brutally_crushed_by_fruit_sound);
      }

      if (player.grounded && !wasGrounded) {
        play_audio(player_land_sound);
      }
    }
  }

  { // jumping
    if (!player.jumping && player.grounded && player.jump_buffer > 0) {
      start_jump();
    }

    player.jump_buffer = Math.max(0, player.jump_buffer - delta);
    player.vel = new Vec(player.vel.x, Math.min(player.vel.y + consts.GRAVITY * delta, consts.MAX_FALL_SPEED));
  }

  { // running
    if (player.push_cooldown > 0) {
      player.push_cooldown -= delta;
    } if (player.rotate_cooldown > 0) {
      player.rotate_cooldown -= delta;
    }

    let pos;
    if (player.facing > 0)
      pos = Vec.add(player.pos, new Vec(player.size.x, 0));
    else if (player.facing < 0)
      pos = Vec.add(player.pos, new Vec(-player.size.x, 0));
    else
      pos = null;

    player.target = pos && collide_target(pos, player.size);

    if (player.target) {
      if (player.push_cooldown <= 0 && input.isPushing()) {
        let oldPos = player.target.pos;
        player.target.pos = new Vec(oldPos.x + player.facing, oldPos.y);

        let valid = !collide_object(player.target);

        if (valid) {
          play_audio(block_slide_sound);
          player.push_cooldown = consts.MOVE_COOLDOWN;
        } else {
          player.target.pos = oldPos;
        }
      }
    }

    let speed_target;
    if (input.isMoving(1)) {
      player.facing = 1;
      speed_target = consts.RUNNING_SPEED_LIMIT;
    } else if (input.isMoving(-1)) {
      player.facing = -1;
      speed_target = -consts.RUNNING_SPEED_LIMIT;
    } else
      speed_target = 0;

    let speed_change;
    if (Math.abs(player.vel.x) > consts.RUNNING_SPEED_LIMIT && Math.sign(player.vel.x) == Math.sign(speed_target)) {
      if (player.grounded)
        speed_change = consts.GROUND_DECELERATION;
      else
        speed_change = consts.AIR_DECELERATION;
    } else {
      if (player.grounded)
        speed_change = consts.GROUND_RUNNING_ACCELERATION;
      else if (Math.sign(player.vel.x) == -Math.sign(speed_target))
        speed_change = consts.AIR_RUNNING_ACCELERATION;
      else
        speed_change = consts.AIR_DECELERATION;
    }

    if (speed_target > player.vel.x) {
      player.vel = new Vec(Math.min(player.vel.x + speed_change * delta, speed_target), player.vel.y);
    } else {
      player.vel = new Vec(Math.max(player.vel.x - speed_change * delta, speed_target), player.vel.y);
    }
  }

  context.clearRect(0, 0, consts.WIDTH * consts.UNIT, consts.HEIGHT * consts.UNIT);

  for (let t of tetronimos) {
    draw_tetronimo(t);
  }

  context.fillStyle = 'black';
  context.fillRect(player.pos.x * consts.UNIT, player.pos.y * consts.UNIT, player.size.x * consts.UNIT, player.size.y * consts.UNIT);

  context.fillStyle = '#ff000080';
  if (player.facing > 0)
    context.fillRect((player.pos.x + player.size.x) * consts.UNIT, player.pos.y * consts.UNIT, player.size.x * consts.UNIT, player.size.y * consts.UNIT);
  else if (player.facing < 0)
    context.fillRect((player.pos.x - player.size.x) * consts.UNIT, player.pos.y * consts.UNIT, player.size.x * consts.UNIT, player.size.y * consts.UNIT);
}

input.onStart(() => {
  if (in_menu) {
    player = {
      target: null,
      facing: 0,
      jumping: false,
      jump_buffer: 0,
      pos: new Vec(4.5, 19),
      size: new Vec(0.8, 0.8),
      vel: new Vec(0, 0),
      grounded: false,
      ceilinged: false,
      push_cooldown: 0,
      rotate_cooldown: 0,
    };

    new_falling();
    in_menu = false;
    previous = 0;
    score = 0;

    background_music.volume = 0.10;
    background_music.loop = true;
    background_music.play();
  }
});

function start_screen(time: number) {
  if (!in_menu) {
    tick(time);
  } else {
    requestAnimationFrame(start_screen); // keep going forever

    time /= 1000;
    let delta = previous ? time - previous : 0;
    previous = time;

    context.clearRect(0, 0, consts.WIDTH * consts.UNIT, consts.HEIGHT * consts.UNIT);
    let high_score = cookie.get_high_score();

    let middle_x = consts.ABSOLUTE_WIDTH / 2;

    let top_y = consts.ABSOLUTE_HEIGHT / 4;

    let middle_y = consts.ABSOLUTE_HEIGHT / 4 * 2;

    let bottom_y = consts.ABSOLUTE_HEIGHT / 4 * 3;

    context.fillStyle = "black";
    context.textAlign = "center";
    context.font = "bold 30px Orbitron";
    context.fillText("Squishy Fruit Mayhem", middle_x, top_y, 300);

    context.fillStyle = "black";
    context.textAlign = "center";
    context.font = "bold 24px Orbitron";
    context.fillText("High Score: " + high_score.toString(), middle_x, middle_y);

    if (time % 2 < 1) {
      context.font = "bold 20px Orbitron";
      context.fillStyle = "black";
      context.fillText("Press Enter to Begin", middle_x, bottom_y);
    } else {
      context.font = "bold 20px Orbitron";
      context.fillStyle = "orange";
      context.fillText("Press Enter to Begin", middle_x, bottom_y);
    }
  }
}

export async function init(canvas: HTMLCanvasElement, canvas_score: HTMLCanvasElement) {
  canvas_score.width = consts.SCORE_ABSOLUTE_WIDTH;
  canvas_score.height = consts.SCORE_ABSOLUTE_HEIGHT;
  canvas_score.style.width = `${consts.SCORE_ABSOLUTE_WIDTH}px`;
  canvas_score.style.height = `${consts.SCORE_ABSOLUTE_HEIGHT}px`;

  canvas.width = consts.ABSOLUTE_WIDTH;
  canvas.height = consts.ABSOLUTE_HEIGHT;
  canvas.style.width = `${consts.ABSOLUTE_WIDTH}px`;
  canvas.style.height = `${consts.ABSOLUTE_HEIGHT}px`;

  await loaded;

  context = canvas.getContext('2d')!;
  score_ctx = canvas_score.getContext('2d')!;
  assert(context != null, 'no context');

  requestAnimationFrame(start_screen);
}
